.User{}
.User:not(:last-child){
  margin-bottom: 10px;
}
.User__body{
  display: flex;
  font-family: 'montserrat';
  color:#fff;
}
.User__avatar{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-right: 15px;
  justify-content: space-between;
  flex: 0 0 150px;
}
.User__avatar_img{
  width: 75px;
  margin-bottom: 5px;
}
.User__avatar_img img{
  width: 100%;
  display: block;
  object-fit: cover;
  border-radius: 20px;
}
.User__avatar_btn{

}
.User__avatar_btn button{
  cursor: pointer;
  font-size: 20px;
  display: block;
  background-color: rgb(214, 224, 65);
  padding: 5px 10px;
  border: 3px solid #000;
}
.User__content{
  flex: 0 1 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgb(212, 105, 105);
  padding: 10px;
  border-radius: 10px;
}
.User__content_top{
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  
}
.User__content_name{}
.User__content_location{
  position: relative;
}
.User__content_location::before{
  content: "from: ";
}
.User__content_footer{}
.User__content_message{}
