.content{
    background-color: rgb(120, 153, 196);
    padding: 10px;
    margin-bottom: 20px;
  }
  .content__block{
    margin-bottom: 10px;
  }
  .contentImg{
    display: block;
    max-width: 200px;
  }
  .Users__paginationItem_active,
  .Users__paginationItem{
    font-size: 18px;
    cursor: pointer;
    border: 1px solid #000;
    width: 40px;
    height: 40px;
    display: inline-block;
    text-align: center;
    transition: .1s all linear;
    border-radius: 14px;
  }
.Users__paginationItem_active{
  border: 1px solid red;
  background-color: #fff;
  transform: scale(1.2);
}
