.Pagination{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.PaginationItem,
.PaginationItemActive{
    cursor: pointer;
    background-color: red;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #fff;
    cursor: pointer;
    &:last-child{
        margin-right: auto;
    }
}
.PaginationItem{
    background-color: red;
}
.PaginationItemActive{
    background-color: #fff;
}